import React from "react";
import MoviesListItem from "../MoviesListItem";

import styles from "./TrendingMovieList.module.css"

const renderCardFilms = ({movies}) => {

    const markup = movies.map(({id,...props}) => <MoviesListItem key={id} id={id} {...props}/>)

    return <ul className={styles.TrendigFilmList}>{markup}</ul>
}

export default renderCardFilms;
