import React from 'react';
import {NavLink} from 'react-router-dom'

import styles from "./MoviesListItem.module.css"

const MoviesListItem = ({id, poster_path, original_title, original_name}) => {

    const img = poster_path ? `https://image.tmdb.org/t/p/w500${poster_path}` : "https://znaiwifi.com/wp-content/uploads/2018/01/hqdefault.jpg";
    const name = original_title ? original_title : original_name;

    return (
        <NavLink to={`/movie/${id}`} className={styles.FilmListLink} >
            <li className={styles.FilmListItem}>
                <img src={img} alt={name} width={320}/>
                <h2>{name}</h2>
            </li>
        </NavLink>
    )
};

export default MoviesListItem;